import React from 'react';
import { Layout, Map, ContactCard, Container } from '../components';

// Data
import pageContext from '../data/pages/contact.json';

const Contact = ({ data }) => {
  const contact = data.site.siteMetadata;

  return (
    <Layout
      pageContext={pageContext}
      heroChildren={<ContactCard contact={contact} />}>
      <Container offsetHero></Container>
      <div style={{ height: '50vh' }}>
        <Map
          apiKey="AIzaSyCR4lpuTYfQn-EWkjyxYDSj3m_BCbO-TOQ"
          center={{ lat: 49.1728135, lng: -123.1361477 }}
        />
      </div>
    </Layout>
  );
};

export default Contact;

export const query = graphql`
  query {
    site {
      siteMetadata {
        address
        city
        province
        postalCode
        phone
        email
      }
    }
  }
`;
